.container {
    background-color: var(--second-color);
    margin-left: auto;
    margin-right: auto;
    max-width: 700px;
  
    margin-top: 20px;
    color: var(--main-color);
    padding: 10px;
    border-radius: 10px;
}
  
.text {
    margin-bottom: 20px;
    margin-top: 20px;
}
  
.form {
    display: flex;
    flex-direction: column;
    margin: 20px 0;
    border: 2px solid var(--main-color);
    border-radius: 5px;
    padding: 10px;
    color: var(--main-color);
}